module.exports = "/* Top container wrapper styles */\n.top-container {\n  min-height: 95vh; /* Display at least 95% of the visual display */\n  position: relative; /* Relative position to place the arrow on the bottom */\n  padding: 1.5em;\n  display: flex;\n  align-items: center;\n}\n/* Top container background image */\n.bg1{\n  background-image: url('home-top-container-bg.jpg');\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-position: bottom;\n  position: relative; /* Relative position to add a pseudo element for the bottom bg */\n}\n/* Add the bottom white background */\n.bg1::after {\n  position: absolute;\n  content: '';\n  width: 100%;\n  height: 140px;\n  bottom: 0;\n  left: 0;\n  background-image: url('Path3.png');\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-position: 70% bottom;\n  -webkit-transform: rotateX(180deg);\n          transform: rotateX(180deg);\n  z-index: 1;\n}\n/* Card container background */\n.bg1 .bg-secondary {\n  background-color:  rgba(250,250,250,.8) !important;\n}\n/* Welcome message card */\n.card1{\n  max-width: 450px;\n  /* margin-left: 6rem; */\n  border-radius: 10px;\n  box-shadow: 4px 6px 10px 2px rgba(0, 0, 0, 0.3);\n}\n/* Arrow container styles */\n.arrow-container {\n  position: absolute; /* Absolute position to stick on the bottom of the container */\n  bottom: 0;\n  /* ALign in the center */\n  left: 50%;\n  -webkit-transform: translateX(-50%);\n          transform: translateX(-50%);\n  z-index: 2;\n}\n.btn-arrow {\n  /* Overwrite bootstrap class */\n  font-size: 40px;\n  padding: 0;\n  line-height: 0;\n  border-radius: 50%;\n  background-color: inherit;\n  /* Fixed size */\n  min-height: 42px;\n  min-width: 42px;\n}\n.btn-arrow:hover, .btn-arrow:focus {\n  color: white;\n  border-color: #333;\n  background-color: #333;\n}\n/* Large screen styles */\n@media screen and (min-width: 992px) {\n  /* Welcome container */\n  .card1 {\n    margin-left: 5em;\n  }\n}\n/* Landscape styles for mobile screens */\n@media screen and (orientation: landscape) and (max-height: 440px) {\n  .card1 {\n    margin: 80px 0;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"